<div class="stat box p-5"><div class="level"><div class="level-left"><div class="level-item"><div class="icon is-size-5" :style="{ borderColor: iconColor }"><font-awesome-icon :icon="icon"></font-awesome-icon></div></div></div><div class="level-right"><div class="level-item"><div class="growth" :class="{ 'is-negative': isNegative, 'is-positive': !isNegative }">{{ growth }}</div></div></div></div><div class="amount is-size-3">{{ amount }}                </div><div class="label is-size-7 mt-2">{{ label }}</div></div>